.cursive {
  font-family: "Amatic SC", cursive;
  font-weight: 700;
}
.cursive {
  font-weight: 700;
  font-family: 'Julius Sans One', sans-serif;
}

.video-responsive {
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute;
}

.App {
  text-align: center;
}

.App-logo {
  height: 80vmin;
  pointer-events: none;
}

.label-logo {
  height: 80vh;
  pointer-events: none;
}

a {
    text-align: center;
    /* font-weight: 600; */
    color: darkred;
    text-decoration: underline;

}
/* #FFD99B; */

.App-header {
  background-color: grey;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: Black;
}


.App-link {
  color: #61dafb;
}

.fill-window {
  height: 100%;
  position: absolute;
  left: 0;
  width: 100%;
  overflow: hidden;
}


.my-node-enter {
  opacity: 0;
}
.my-node-enter-active {
  opacity: 1;
  transition: opacity 200ms;
}
.my-node-exit {
  opacity: 1;
}
.my-node-exit-active {
  opacity: 0;
  transition: opacity 200ms;
}


.sidebar {
    background-color: white;
    width: 10vw;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    min-height: 100vh !important;
    z-index: 100;
    padding: 20% 0 0;
    text-decoration: none !important;
}

@media screen and (max-width: 600px) {
    .sidebar {
        background-color: white;
        width: 15vw;
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        min-height: 100vh !important;
        z-index: 100;
        padding: 20% 0 0;
        text-decoration: none !important;
    }    
}
#sidebar-wrapper{
    min-height: 100vh !important;
    width: 100vw;
    margin-left: -1rem;
    transition: margin .25s ease-out;
    text-decoration: none;
}

#sidebar-wrapper .sidebar-heading {
    padding: 0.875rem 1.25rem;
    font-size: 1.2rem;
}

#page-content-wrapper {
    min-width: 0;
    width: 100%;
}

